import { createRouter, createWebHashHistory } from 'vue-router'
import index from '../views'
import home from '../views/pages/home.vue'
import login from '../views/pages/login.vue'
import newsList from '@/views/pages/news/list'
import xiaoshourenyuanList from '@/views/pages/xiaoshourenyuan/list'
import xiaoshourenyuanDetail from '@/views/pages/xiaoshourenyuan/formModel'
import xiaoshourenyuanAdd from '@/views/pages/xiaoshourenyuan/formAdd'
import kehuList from '@/views/pages/kehu/list'
import kehuDetail from '@/views/pages/kehu/formModel'
import kehuAdd from '@/views/pages/kehu/formAdd'
import kehuRegister from '@/views/pages/kehu/register'
import kehuCenter from '@/views/pages/kehu/center'
import qicheleixingList from '@/views/pages/qicheleixing/list'
import qicheleixingDetail from '@/views/pages/qicheleixing/formModel'
import qicheleixingAdd from '@/views/pages/qicheleixing/formAdd'
import qichexinxiList from '@/views/pages/qichexinxi/list'
import qichexinxiDetail from '@/views/pages/qichexinxi/formModel'
import qichexinxiAdd from '@/views/pages/qichexinxi/formAdd'
import storeupList from '@/views/pages/storeup/list'
import xiaoshoudingdanList from '@/views/pages/xiaoshoudingdan/list'
import xiaoshoudingdanDetail from '@/views/pages/xiaoshoudingdan/formModel'
import xiaoshoudingdanAdd from '@/views/pages/xiaoshoudingdan/formAdd'
import yuyueshijiaList from '@/views/pages/yuyueshijia/list'
import yuyueshijiaDetail from '@/views/pages/yuyueshijia/formModel'
import yuyueshijiaAdd from '@/views/pages/yuyueshijia/formAdd'

const routes = [{
		path: '/',
		redirect: '/index/home'
	},
	{
		path: '/index',
		component: index,
		children: [{
			path: 'home',
			component: home
		}
		, {
			path: 'newsList',
			component: newsList
		}
		, {
			path: 'xiaoshourenyuanList',
			component: xiaoshourenyuanList
		}, {
			path: 'xiaoshourenyuanDetail',
			component: xiaoshourenyuanDetail
		}, {
			path: 'xiaoshourenyuanAdd',
			component: xiaoshourenyuanAdd
		}
		, {
			path: 'kehuList',
			component: kehuList
		}, {
			path: 'kehuDetail',
			component: kehuDetail
		}, {
			path: 'kehuAdd',
			component: kehuAdd
		}
		, {
			path: 'kehuCenter',
			component: kehuCenter
		}
		, {
			path: 'qicheleixingList',
			component: qicheleixingList
		}, {
			path: 'qicheleixingDetail',
			component: qicheleixingDetail
		}, {
			path: 'qicheleixingAdd',
			component: qicheleixingAdd
		}
		, {
			path: 'qichexinxiList',
			component: qichexinxiList
		}, {
			path: 'qichexinxiDetail',
			component: qichexinxiDetail
		}, {
			path: 'qichexinxiAdd',
			component: qichexinxiAdd
		}
		, {
			path: 'storeupList',
			component: storeupList
		}
		, {
			path: 'xiaoshoudingdanList',
			component: xiaoshoudingdanList
		}, {
			path: 'xiaoshoudingdanDetail',
			component: xiaoshoudingdanDetail
		}, {
			path: 'xiaoshoudingdanAdd',
			component: xiaoshoudingdanAdd
		}
		, {
			path: 'yuyueshijiaList',
			component: yuyueshijiaList
		}, {
			path: 'yuyueshijiaDetail',
			component: yuyueshijiaDetail
		}, {
			path: 'yuyueshijiaAdd',
			component: yuyueshijiaAdd
		}
		]
	},
	{
		path: '/login',
		component: login
	}
	,{
		path: '/kehuRegister',
		component: kehuRegister
	}
]

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes
})

export default router
