
<template>
	<div class="app-contain" :style='{"minHeight":"100vh","padding":"0","margin":"20px auto 60px","borderRadius":"0px","background":"#fff","width":"64%","position":"relative","height":"100%"}'>
		<div class="bread_view">
			<el-breadcrumb separator="/" class="breadcrumb">
				<el-breadcrumb-item class="first_breadcrumb" :to="{ path: '/' }">首页</el-breadcrumb-item>
				<el-breadcrumb-item class="second_breadcrumb" v-for="(item,index) in breadList" :key="index">{{item.name}}</el-breadcrumb-item>
			</el-breadcrumb>
		</div>
		<el-form ref="formRef" :model="form" class="add_form" label-width="120px" :rules="rules">
			<el-row>
				<el-col :span="12">
					<el-form-item label="汽车编号" prop="qichebianhao">
						<el-input class="list_inp" v-model="form.qichebianhao" placeholder="请输入汽车编号" readonly></el-input>
					</el-form-item>
				</el-col>
				<el-col :span="12">
					<el-form-item label="汽车名称" prop="qichemingcheng">
						<el-input class="list_inp" v-model="form.qichemingcheng" placeholder="汽车名称"
							 type="text" 							:readonly="!isAdd||disabledForm.qichemingcheng?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="汽车图片" prop="qichetupian">
						<uploads
							:disabled="!isAdd||disabledForm.qichetupian?true:false"
							action="file/upload" 
							tip="请上传汽车图片" 
							:limit="3" 
							style="width: 100%;text-align: left;"
							:fileUrls="form.qichetupian?form.qichetupian:''" 
							@change="qichetupianUploadSuccess">
						</uploads>
					</el-form-item>
				</el-col>
				<el-col :span="12">
					<el-form-item label="汽车类型" prop="qicheleixing">
						<el-select
							class="list_sel"
							:disabled="!isAdd||disabledForm.qicheleixing?true:false"
							v-model="form.qicheleixing" 
							placeholder="请选择汽车类型"
							style="width:100%;"
							>
							<el-option v-for="(item,index) in qicheleixingLists" :label="item"
								:value="item"
								>
							</el-option>
						</el-select>
					</el-form-item>
				</el-col>
				<el-col :span="12">
					<el-form-item label="车身颜色" prop="cheshenyanse">
						<el-input class="list_inp" v-model="form.cheshenyanse" placeholder="车身颜色"
							 type="text" 							:readonly="!isAdd||disabledForm.cheshenyanse?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="汽车品牌" prop="qichepinpai">
						<el-input class="list_inp" v-model="form.qichepinpai" placeholder="汽车品牌"
							 type="text" 							:readonly="!isAdd||disabledForm.qichepinpai?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="换档方式" prop="huandangfangshi">
						<el-select
							class="list_sel"
							:disabled="!isAdd||disabledForm.huandangfangshi?true:false"
							v-model="form.huandangfangshi" 
							placeholder="请选择换档方式"
							style="width:100%;"
							>
							<el-option v-for="(item,index) in huandangfangshiLists" :label="item"
								:value="item"
								>
							</el-option>
						</el-select>
					</el-form-item>
				</el-col>
				<el-col :span="12">
					<el-form-item label="天窗" prop="tianchuang">
						<el-select
							class="list_sel"
							:disabled="!isAdd||disabledForm.tianchuang?true:false"
							v-model="form.tianchuang" 
							placeholder="请选择天窗"
							style="width:100%;"
							>
							<el-option v-for="(item,index) in tianchuangLists" :label="item"
								:value="item"
								>
							</el-option>
						</el-select>
					</el-form-item>
				</el-col>
				<el-col :span="12">
					<el-form-item label="车座" prop="chezuo">
						<el-select
							class="list_sel"
							:disabled="!isAdd||disabledForm.chezuo?true:false"
							v-model="form.chezuo" 
							placeholder="请选择车座"
							style="width:100%;"
							>
							<el-option v-for="(item,index) in chezuoLists" :label="item"
								:value="item"
								>
							</el-option>
						</el-select>
					</el-form-item>
				</el-col>
				<el-col :span="12">
					<el-form-item label="销售价格" prop="xiaoshoujiage">
						<el-input class="list_inp" v-model.number="form.xiaoshoujiage" placeholder="销售价格"
							 type="number" 							:readonly="!isAdd||disabledForm.xiaoshoujiage?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="数量" prop="shuliang">
						<el-input class="list_inp" v-model.number="form.shuliang" placeholder="数量"
							 type="text" 							:readonly="!isAdd||disabledForm.shuliang?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="销售账号" prop="xiaoshouzhanghao">
						<el-input class="list_inp" v-model="form.xiaoshouzhanghao" placeholder="销售账号"
							 type="text" 							:readonly="!isAdd||disabledForm.xiaoshouzhanghao?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="销售姓名" prop="xiaoshouxingming">
						<el-input class="list_inp" v-model="form.xiaoshouxingming" placeholder="销售姓名"
							 type="text" 							:readonly="!isAdd||disabledForm.xiaoshouxingming?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="24">
					<el-form-item label="汽车详情" prop="qichexiangqing">
						<editor class="list_editor" :value="form.qichexiangqing" placeholder="请输入汽车详情" :readonly="!isAdd||disabledForm.qichexiangqing?true:false"
							@change="(e)=>editorChange(e,'qichexiangqing')"></editor>
					</el-form-item>
				</el-col>
			</el-row>
			<div class="formModel_btn_box">
				<el-button class="formModel_cancel" @click="backClick">取消</el-button>
				<el-button class="formModel_confirm" @click="save"
					type="success"
					>
					保存
				</el-button>
			</div>
		</el-form>
	</div>
</template>
<script setup>
	import {
		ref,
		getCurrentInstance,
		watch,
		onUnmounted,
		onMounted,
		nextTick,
		computed
	} from 'vue';
	import {
		useRoute,
		useRouter
	} from 'vue-router';
	const context = getCurrentInstance()?.appContext.config.globalProperties;
	const route = useRoute()
	const router = useRouter()
	//基础信息
	const tableName = 'qichexinxi'
	const formName = '汽车信息'
	//基础信息
	const breadList = ref([{
		name: formName
	}])
	//获取唯一标识
	const getUUID =()=> {
      return new Date().getTime();
    }
	//form表单
	const form = ref({
		qichebianhao: getUUID(),
		qichemingcheng: '',
		qichetupian: '',
		qicheleixing: '',
		cheshenyanse: '',
		qichepinpai: '',
		huandangfangshi: '',
		tianchuang: '',
		chezuo: '',
		xiaoshoujiage: '',
		shuliang: '',
		qichexiangqing: '',
		storeupnum: '0',
		xiaoshouzhanghao: '',
		xiaoshouxingming: '',
	})
	const formRef = ref(null)
	const id = ref(0)
	const type = ref('')
	const disabledForm = ref({
		qichebianhao : false,
		qichemingcheng : false,
		qichetupian : false,
		qicheleixing : false,
		cheshenyanse : false,
		qichepinpai : false,
		huandangfangshi : false,
		tianchuang : false,
		chezuo : false,
		xiaoshoujiage : false,
		shuliang : false,
		qichexiangqing : false,
		storeupnum : false,
		xiaoshouzhanghao : false,
		xiaoshouxingming : false,
	})
	const isAdd = ref(false)
	//表单验证
	//匹配整数
	const validateIntNumber = (rule, value, callback) => {
		if (!value) {
			callback();
		} else if (!context?.$toolUtil.isIntNumer(value)) {
			callback(new Error("请输入整数"));
		} else {
			callback();
		}
	}
	//匹配数字
	const validateNumber = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isNumber(value)) {
			callback(new Error("请输入数字"));
		} else {
			callback();
		}
	}
	//匹配手机号码
	const validateMobile = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isMobile(value)) {
			callback(new Error("请输入正确的手机号码"));
		} else {
			callback();
		}
	}
	//匹配电话号码
	const validatePhone = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isPhone(value)) {
			callback(new Error("请输入正确的电话号码"));
		} else {
			callback();
		}
	}
	//匹配邮箱
	const validateEmail = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isEmail(value)) {
			callback(new Error("请输入正确的邮箱地址"));
		} else {
			callback();
		}
	}
	//匹配身份证
	const validateIdCard = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.checkIdCard(value)) {
			callback(new Error("请输入正确的身份证号码"));
		} else {
			callback();
		}
	}
	//匹配网站地址
	const validateUrl = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isURL(value)) {
			callback(new Error("请输入正确的URL地址"));
		} else {
			callback();
		}
	}
	const rules = ref({
		qichebianhao: [
		],
		qichemingcheng: [
			{required: true,message: '请输入',trigger: 'blur'}, 
		],
		qichetupian: [
		],
		qicheleixing: [
			{required: true,message: '请输入',trigger: 'blur'}, 
		],
		cheshenyanse: [
			{required: true,message: '请输入',trigger: 'blur'}, 
		],
		qichepinpai: [
			{required: true,message: '请输入',trigger: 'blur'}, 
		],
		huandangfangshi: [
		],
		tianchuang: [
		],
		chezuo: [
		],
		xiaoshoujiage: [
			{required: true,message: '请输入',trigger: 'blur'}, 
			{ validator: validateNumber, trigger: 'blur' },
		],
		shuliang: [
			{ validator: validateIntNumber, trigger: 'blur' },
		],
		qichexiangqing: [
		],
		storeupnum: [
			{ validator: validateIntNumber, trigger: 'blur' },
		],
		xiaoshouzhanghao: [
		],
		xiaoshouxingming: [
		],
	})
	//汽车图片上传回调
	const qichetupianUploadSuccess=(e)=>{
		form.value.qichetupian = e
	}
	//汽车类型列表
	const qicheleixingLists = ref([])
	//换档方式列表
	const huandangfangshiLists = ref([])
	//天窗列表
	const tianchuangLists = ref([])
	//车座列表
	const chezuoLists = ref([])
	//methods

	//methods
	//获取info
	const getInfo = ()=>{
		context?.$http({
			url: `${tableName}/info/${id.value}`,
			method: 'get'
		}).then(res => {
			let reg=new RegExp('../../../file','g')
			res.data.data.qichexiangqing = res.data.data.qichexiangqing.replace(reg,'../../../cl32932195/file');
			form.value = res.data.data
		})
	}
	const crossRow = ref('')
	const crossTable = ref('')
	const crossTips = ref('')
	const crossColumnName = ref('')
	const crossColumnValue = ref('')
	//初始化
	const init = (formId=null,formType='add',formNames='',row=null,table=null,statusColumnName=null,tips=null,statusColumnValue=null) => {
		if(formId){
			id.value = formId
			type.value = formType
		}
		if(formType == 'add'){
			isAdd.value = true
		}else if(formType == 'info'){
			isAdd.value = false
			getInfo()
		}else if(formType == 'edit'){
			isAdd.value = true
			getInfo()
		}
		else if(formType == 'cross'){
			isAdd.value = true
			// getInfo()
			for(let x in row){
				if(x=='qichebianhao'){
					form.value.qichebianhao = row[x];
					disabledForm.value.qichebianhao = true;
					continue;
				}
				if(x=='qichemingcheng'){
					form.value.qichemingcheng = row[x];
					disabledForm.value.qichemingcheng = true;
					continue;
				}
				if(x=='qichetupian'){
					form.value.qichetupian = row[x];
					disabledForm.value.qichetupian = true;
					continue;
				}
				if(x=='qicheleixing'){
					form.value.qicheleixing = row[x];
					disabledForm.value.qicheleixing = true;
					continue;
				}
				if(x=='cheshenyanse'){
					form.value.cheshenyanse = row[x];
					disabledForm.value.cheshenyanse = true;
					continue;
				}
				if(x=='qichepinpai'){
					form.value.qichepinpai = row[x];
					disabledForm.value.qichepinpai = true;
					continue;
				}
				if(x=='huandangfangshi'){
					form.value.huandangfangshi = row[x];
					disabledForm.value.huandangfangshi = true;
					continue;
				}
				if(x=='tianchuang'){
					form.value.tianchuang = row[x];
					disabledForm.value.tianchuang = true;
					continue;
				}
				if(x=='chezuo'){
					form.value.chezuo = row[x];
					disabledForm.value.chezuo = true;
					continue;
				}
				if(x=='xiaoshoujiage'){
					form.value.xiaoshoujiage = row[x];
					disabledForm.value.xiaoshoujiage = true;
					continue;
				}
				if(x=='shuliang'){
					form.value.shuliang = row[x];
					disabledForm.value.shuliang = true;
					continue;
				}
				if(x=='qichexiangqing'){
					form.value.qichexiangqing = row[x];
					disabledForm.value.qichexiangqing = true;
					continue;
				}
				if(x=='storeupnum'){
					form.value.storeupnum = row[x];
					disabledForm.value.storeupnum = true;
					continue;
				}
				if(x=='xiaoshouzhanghao'){
					form.value.xiaoshouzhanghao = row[x];
					disabledForm.value.xiaoshouzhanghao = true;
					continue;
				}
				if(x=='xiaoshouxingming'){
					form.value.xiaoshouxingming = row[x];
					disabledForm.value.xiaoshouxingming = true;
					continue;
				}
			}
			if(row){
				crossRow.value = row
			}
			if(table){
				crossTable.value = table
			}
			if(tips){
				crossTips.value = tips
			}
			if(statusColumnName){
				crossColumnName.value = statusColumnName
			}
			if(statusColumnValue){
				crossColumnValue.value = statusColumnValue
			}
			form.value.storeupnum='0'
		}
		context?.$http({
			url: `${context?.$toolUtil.storageGet('frontSessionTable')}/session`,
			method: 'get'
		}).then(res => {
			var json = res.data.data
			if(json.hasOwnProperty('xiaoshouzhanghao') && context?.$toolUtil.storageGet("frontRole")!="管理员"){
				form.value.xiaoshouzhanghao = json.xiaoshouzhanghao
				disabledForm.value.xiaoshouzhanghao = true;
			}
			if(json.hasOwnProperty('xiaoshouxingming') && context?.$toolUtil.storageGet("frontRole")!="管理员"){
				form.value.xiaoshouxingming = json.xiaoshouxingming
				disabledForm.value.xiaoshouxingming = true;
			}
		})
		context?.$http({
			url: `option/qicheleixing/qicheleixing`,
			method: 'get'
		}).then(res=>{
			qicheleixingLists.value = res.data.data
		})
		huandangfangshiLists.value = "自动挡,手动挡".split(',')
		tianchuangLists.value = "有天窗,无天窗".split(',')
		chezuoLists.value = "两座,四座,五座,七座".split(',')
	}
	//初始化
	//取消
	const backClick = () => {
		history.back()
	}
	//富文本数据回调
	const editorChange = (e,name) =>{
		form.value[name] = e
	}
	//提交
	const save=()=>{
		if(form.value.qichetupian!=null) {
			form.value.qichetupian = form.value.qichetupian.replace(new RegExp(context?.$config.url,"g"),"");
		}
		var table = crossTable.value
		var objcross = JSON.parse(JSON.stringify(crossRow.value))
		let crossUserId = ''
		let crossRefId = ''
		let crossOptNum = ''
		if(type.value == 'cross'){
			if(crossColumnName.value!=''){
				if(!crossColumnName.value.startsWith('[')){
					for(let o in objcross){
						if(o == crossColumnName.value){
							objcross[o] = crossColumnValue.value
						}
					}
					//修改跨表数据
					changeCrossData(objcross)
				}else{
					crossUserId = context?.$toolUtil.storageGet('userid')
					crossRefId = objcross['id']
					crossOptNum = crossColumnName.value.replace(/\[/,"").replace(/\]/,"")
				}
			}
		}
		formRef.value.validate((valid)=>{
			if(valid){
				if(crossUserId&&crossRefId){
					form.value.crossuserid = crossUserId
					form.value.crossrefid = crossRefId
					let params = {
						page: 1,
						limit: 1000, 
						crossuserid:form.value.crossuserid,
						crossrefid:form.value.crossrefid,
					}
					context?.$http({
						url: `${tableName}/page`,
						method: 'get', 
						params: params 
					}).then(res=>{
						if(res.data.data.total>=crossOptNum){
							context?.$toolUtil.message(`${crossTips.value}`,'error')
							return false
						}else{
							context?.$http({
								url: `${tableName}/${!form.value.id ? "save" : "update"}`,
								method: 'post', 
								data: form.value 
							}).then(res=>{
								context?.$toolUtil.message(`操作成功`,'success',()=>{
									history.back()
								})
							})
						}
					})
				}else{
					context?.$http({
						url: `${tableName}/${!form.value.id ? "save" : "update"}`,
						method: 'post', 
						data: form.value 
					}).then(res=>{
						context?.$toolUtil.message(`操作成功`,'success',()=>{
							history.back()
						})
					})
				}
			}
		})
	}
	//修改跨表数据
	const changeCrossData=(row)=>{
		context?.$http({
			url: `${crossTable.value}/update`,
			method: 'post',
			data: row
		}).then(res=>{})
	}
	onMounted(()=>{
		type.value = route.query.type?route.query.type:'add'
		let row = null
		let table = null
		let statusColumnName = null
		let tips = null
		let statusColumnValue = null
		if(type.value == 'cross'){
			row = context?.$toolUtil.storageGet('crossObj')?JSON.parse(context?.$toolUtil.storageGet('crossObj')):{}
			table = context?.$toolUtil.storageGet('crossTable')
			statusColumnName = context?.$toolUtil.storageGet('crossStatusColumnName')
			tips = context?.$toolUtil.storageGet('crossTips')
			statusColumnValue = context?.$toolUtil.storageGet('crossStatusColumnValue')
		}
		init(route.query.id?route.query.id:null, type.value,'', row, table, statusColumnName, tips, statusColumnValue)
	})
	
</script>
<style lang="scss" scoped>
	// 面包屑盒子
	.bread_view {
		border-radius: 0px;
		padding: 12px 20px;
		margin: 0px auto;
		background: none;
		width: 100%;
		border-color: #eee;
		border-width: 0 0 0px;
		position: relative;
		border-style: solid;
		:deep(.breadcrumb) {
			font-size: 14px;
			line-height: 1;
			.el-breadcrumb__separator {
				margin: 0 9px;
				color: #999;
				font-weight: 500;
			}
			.first_breadcrumb {
				.el-breadcrumb__inner {
					color: #333;
					display: inline-block;
				}
			}
			.second_breadcrumb {
				.el-breadcrumb__inner {
					color: #999;
					display: inline-block;
				}
			}
		}
	}
	// 表单
	.add_form{
		border: 0px solid #2da065;
		border-radius: 0px;
		padding: 30px 7%;
		background: none;
		// form item
		:deep(.el-form-item) {
			border: 0px solid #eee;
			padding: 6px 0;
			margin: 0 0 20px 0;
			background: none;
			display: flex;
			//label
			.el-form-item__label {
			 background: none;
			 display: block;
			 width: auto;
