
<template>
	<div class="app-contain" :style='{"minHeight":"100vh","padding":"0","margin":"20px auto 60px","borderRadius":"0px","background":"#fff","width":"64%","position":"relative","height":"100%"}'>
		<div class="bread_view">
			<el-breadcrumb separator="/" class="breadcrumb">
				<el-breadcrumb-item class="first_breadcrumb" :to="{ path: '/' }">首页</el-breadcrumb-item>
				<el-breadcrumb-item class="second_breadcrumb" v-for="(item,index) in breadList" :key="index">{{item.name}}</el-breadcrumb-item>
			</el-breadcrumb>
		</div>
		<el-form ref="formRef" :model="form" class="add_form" label-width="120px" :rules="rules">
			<el-row>
				<el-col :span="12">
					<el-form-item label="销售编号" prop="xiaoshoubianhao">
						<el-input class="list_inp" v-model="form.xiaoshoubianhao" placeholder="请输入销售编号" readonly></el-input>
					</el-form-item>
				</el-col>
				<el-col :span="12">
					<el-form-item label="汽车名称" prop="qichemingcheng">
						<el-input class="list_inp" v-model="form.qichemingcheng" placeholder="汽车名称"
							 type="text" 							:readonly="!isAdd||disabledForm.qichemingcheng?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="汽车类型" prop="qicheleixing">
						<el-input class="list_inp" v-model="form.qicheleixing" placeholder="汽车类型"
							 type="text" 							:readonly="!isAdd||disabledForm.qicheleixing?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="车身颜色" prop="cheshenyanse">
						<el-input class="list_inp" v-model="form.cheshenyanse" placeholder="车身颜色"
							 type="text" 							:readonly="!isAdd||disabledForm.cheshenyanse?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="汽车品牌" prop="qichepinpai">
						<el-input class="list_inp" v-model="form.qichepinpai" placeholder="汽车品牌"
							 type="text" 							:readonly="!isAdd||disabledForm.qichepinpai?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="换档方式" prop="huandangfangshi">
						<el-input class="list_inp" v-model="form.huandangfangshi" placeholder="换档方式"
							 type="text" 							:readonly="!isAdd||disabledForm.huandangfangshi?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="天窗" prop="tianchuang">
						<el-input class="list_inp" v-model="form.tianchuang" placeholder="天窗"
							 type="text" 							:readonly="!isAdd||disabledForm.tianchuang?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="车座" prop="chezuo">
						<el-input class="list_inp" v-model="form.chezuo" placeholder="车座"
							 type="text" 							:readonly="!isAdd||disabledForm.chezuo?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="销售价格" prop="xiaoshoujiage">
						<el-input class="list_inp" v-model.number="form.xiaoshoujiage" placeholder="销售价格"
							 type="number" 							:readonly="!isAdd||disabledForm.xiaoshoujiage?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="数量" prop="shuliang">
						<el-input class="list_inp" v-model.number="form.shuliang" placeholder="数量"
							 type="text" 							:readonly="!isAdd||disabledForm.shuliang?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="总价" prop="zongjia">
						<el-input class="list_inp" v-model="zongjia" placeholder="总价" readonly></el-input>
					</el-form-item>
				</el-col>
				<el-col :span="12">
					<el-form-item label="销售时间" prop="xiaoshoushijian">
						<el-date-picker
							class="list_date"
							v-model="form.xiaoshoushijian"
							format="YYYY-MM-DD HH:mm:ss"
							value-format="YYYY-MM-DD HH:mm:ss"
							type="datetime"
							style="width:100%;"
							:readonly="!isAdd||disabledForm.xiaoshoushijian?true:false"
							placeholder="请选择销售时间" />
					</el-form-item>
				</el-col>
				<el-col :span="12">
					<el-form-item label="客户账号" prop="kehuzhanghao">
						<el-input class="list_inp" v-model="form.kehuzhanghao" placeholder="客户账号"
							 type="text" 							:readonly="!isAdd||disabledForm.kehuzhanghao?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="客户姓名" prop="kehuxingming">
						<el-input class="list_inp" v-model="form.kehuxingming" placeholder="客户姓名"
							 type="text" 							:readonly="!isAdd||disabledForm.kehuxingming?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="手机号码" prop="shoujihaoma">
						<el-input class="list_inp" v-model="form.shoujihaoma" placeholder="手机号码"
							 type="text" 							:readonly="!isAdd||disabledForm.shoujihaoma?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="销售账号" prop="xiaoshouzhanghao">
						<el-input class="list_inp" v-model="form.xiaoshouzhanghao" placeholder="销售账号"
							 type="text" 							:readonly="!isAdd||disabledForm.xiaoshouzhanghao?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="销售姓名" prop="xiaoshouxingming">
						<el-input class="list_inp" v-model="form.xiaoshouxingming" placeholder="销售姓名"
							 type="text" 							:readonly="!isAdd||disabledForm.xiaoshouxingming?true:false" />
					</el-form-item>
				</el-col>

			</el-row>
			<div class="formModel_btn_box">
				<el-button class="formModel_cancel" @click="backClick">取消</el-button>
				<el-button class="formModel_confirm" @click="save"
					type="success"
					>
					保存
				</el-button>
			</div>
		</el-form>
	</div>
</template>
<script setup>
	import {
		ref,
		getCurrentInstance,
		watch,
		onUnmounted,
		onMounted,
		nextTick,
		computed
	} from 'vue';
	import {
		useRoute,
		useRouter
	} from 'vue-router';
	const context = getCurrentInstance()?.appContext.config.globalProperties;
	const route = useRoute()
	const router = useRouter()
	//基础信息
	const tableName = 'xiaoshoudingdan'
	const formName = '销售订单'
	//基础信息
	const breadList = ref([{
		name: formName
	}])
	//获取唯一标识
	const getUUID =()=> {
      return new Date().getTime();
    }
	//form表单
	const form = ref({
		xiaoshoubianhao: getUUID(),
		qichemingcheng: '',
		qicheleixing: '',
		cheshenyanse: '',
		qichepinpai: '',
		huandangfangshi: '',
		tianchuang: '',
		chezuo: '',
		xiaoshoujiage: '',
		shuliang: '',
		zongjia: '',
		xiaoshoushijian: '',
		kehuzhanghao: '',
		kehuxingming: '',
		shoujihaoma: '',
		ispay: '未支付',
		xiaoshouzhanghao: '',
		xiaoshouxingming: '',
	})
	const formRef = ref(null)
	const id = ref(0)
	const type = ref('')
	const disabledForm = ref({
		xiaoshoubianhao : false,
		qichemingcheng : false,
		qicheleixing : false,
		cheshenyanse : false,
		qichepinpai : false,
		huandangfangshi : false,
		tianchuang : false,
		chezuo : false,
		xiaoshoujiage : false,
		shuliang : false,
		zongjia : false,
		xiaoshoushijian : false,
		kehuzhanghao : false,
		kehuxingming : false,
		shoujihaoma : false,
		ispay : false,
		xiaoshouzhanghao : false,
		xiaoshouxingming : false,
	})
	const isAdd = ref(false)
	//表单验证
	//匹配整数
	const validateIntNumber = (rule, value, callback) => {
		if (!value) {
			callback();
		} else if (!context?.$toolUtil.isIntNumer(value)) {
			callback(new Error("请输入整数"));
		} else {
			callback();
		}
	}
	//匹配数字
	const validateNumber = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isNumber(value)) {
			callback(new Error("请输入数字"));
		} else {
			callback();
		}
	}
	//匹配手机号码
	const validateMobile = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isMobile(value)) {
			callback(new Error("请输入正确的手机号码"));
		} else {
			callback();
		}
	}
	//匹配电话号码
	const validatePhone = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isPhone(value)) {
			callback(new Error("请输入正确的电话号码"));
		} else {
			callback();
		}
	}
	//匹配邮箱
	const validateEmail = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isEmail(value)) {
			callback(new Error("请输入正确的邮箱地址"));
		} else {
			callback();
		}
	}
	//匹配身份证
	const validateIdCard = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.checkIdCard(value)) {
			callback(new Error("请输入正确的身份证号码"));
		} else {
			callback();
		}
	}
	//匹配网站地址
	const validateUrl = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isURL(value)) {
			callback(new Error("请输入正确的URL地址"));
		} else {
			callback();
		}
	}
	const rules = ref({
		xiaoshoubianhao: [
		],
		qichemingcheng: [
			{required: true,message: '请输入',trigger: 'blur'}, 
		],
		qicheleixing: [
			{required: true,message: '请输入',trigger: 'blur'}, 
		],
		cheshenyanse: [
			{required: true,message: '请输入',trigger: 'blur'}, 
		],
		qichepinpai: [
			{required: true,message: '请输入',trigger: 'blur'}, 
		],
		huandangfangshi: [
		],
		tianchuang: [
		],
		chezuo: [
		],
		xiaoshoujiage: [
			{required: true,message: '请输入',trigger: 'blur'}, 
			{ validator: validateNumber, trigger: 'blur' },
		],
		shuliang: [
			{ validator: validateIntNumber, trigger: 'blur' },
		],
		zongjia: [
			{ validator: validateNumber, trigger: 'blur' },
		],
		xiaoshoushijian: [
		],
		kehuzhanghao: [
			{required: true,message: '请输入',trigger: 'blur'}, 
		],
		kehuxingming: [
		],
		shoujihaoma: [
		],
		ispay: [
		],
		xiaoshouzhanghao: [
		],
		xiaoshouxingming: [
		],
	})
	const zongjia =computed(()=>{
		let c = form.value
		let a = c.xiaoshoujiage*c.shuliang
		form.value.zongjia = a?a.toFixed(2):0
		return a?a.toFixed(2):0
	})
	//methods

	//methods
	//获取info
	const getInfo = ()=>{
		context?.$http({
			url: `${tableName}/info/${id.value}`,
			method: 'get'
		}).then(res => {
			let reg=new RegExp('../../../file','g')
			form.value = res.data.data
		})
	}
	const crossRow = ref('')
	const crossTable = ref('')
	const crossTips = ref('')
	const crossColumnName = ref('')
	const crossColumnValue = ref('')
	//初始化
	const init = (formId=null,formType='add',formNames='',row=null,table=null,statusColumnName=null,tips=null,statusColumnValue=null) => {
			form.value.xiaoshoushijian = context?.$toolUtil.getCurDateTime()
		if(formId){
			id.value = formId
			type.value = formType
		}
		if(formType == 'add'){
			isAdd.value = true
		}else if(formType == 'info'){
			isAdd.value = false
			getInfo()
		}else if(formType == 'edit'){
			isAdd.value = true
			getInfo()
		}
		else if(formType == 'cross'){
			isAdd.value = true
			// getInfo()
			for(let x in row){
				if(x=='xiaoshoubianhao'){
					form.value.xiaoshoubianhao = row[x];
					disabledForm.value.xiaoshoubianhao = true;
					continue;
				}
				if(x=='qichemingcheng'){
					form.value.qichemingcheng = row[x];
					disabledForm.value.qichemingcheng = true;
					continue;
				}
				if(x=='qicheleixing'){
					form.value.qicheleixing = row[x];
					disabledForm.value.qicheleixing = true;
					continue;
				}
				if(x=='cheshenyanse'){
					form.value.cheshenyanse = row[x];
					disabledForm.value.cheshenyanse = true;
					continue;
				}
				if(x=='qichepinpai'){
					form.value.qichepinpai = row[x];
					disabledForm.value.qichepinpai = true;
					continue;
				}
				if(x=='huandangfangshi'){
					form.value.huandangfangshi = row[x];
					disabledForm.value.huandangfangshi = true;
					continue;
				}
				if(x=='tianchuang'){
					form.value.tianchuang = row[x];
					disabledForm.value.tianchuang = true;
					continue;
				}
				if(x=='chezuo'){
					form.value.chezuo = row[x];
					disabledForm.value.chezuo = true;
					continue;
				}
				if(x=='xiaoshoujiage'){
					form.value.xiaoshoujiage = row[x];
					disabledForm.value.xiaoshoujiage = true;
					continue;
				}
				if(x=='shuliang'){
					form.value.shuliang = row[x];
					disabledForm.value.shuliang = true;
					continue;
				}
				if(x=='zongjia'){
					form.value.zongjia = row[x];
					disabledForm.value.zongjia = true;
					continue;
				}
				if(x=='xiaoshoushijian'){
					form.value.xiaoshoushijian = row[x];
					disabledForm.value.xiaoshoushijian = true;
					continue;
				}
				if(x=='kehuzhanghao'){
					form.value.kehuzhanghao = row[x];
					disabledForm.value.kehuzhanghao = true;
					continue;
				}
				if(x=='kehuxingming'){
					form.value.kehuxingming = row[x];
					disabledForm.value.kehuxingming = true;
					continue;
				}
				if(x=='shoujihaoma'){
					form.value.shoujihaoma = row[x];
					disabledForm.value.shoujihaoma = true;
					continue;
				}
				if(x=='xiaoshouzhanghao'){
					form.value.xiaoshouzhanghao = row[x];
					disabledForm.value.xiaoshouzhanghao = true;
					continue;
				}
				if(x=='xiaoshouxingming'){
					form.value.xiaoshouxingming = row[x];
					disabledForm.value.xiaoshouxingming = true;
					continue;
				}
			}
			if(row){
				crossRow.value = row
			}
			if(table){
				crossTable.value = table
			}
			if(tips){
				crossTips.value = tips
			}
			if(statusColumnName){
				crossColumnName.value = statusColumnName
			}
			if(statusColumnValue){
				crossColumnValue.value = statusColumnValue
			}
			form.value.shuliang = 0
			disabledForm.value.shuliang = false;
			form.value.ispay='未支付'
		}
		context?.$http({
			url: `${context?.$toolUtil.storageGet('frontSessionTable')}/session`,
			method: 'get'
		}).then(res => {
			var json = res.data.data
			if(json.hasOwnProperty('kehuzhanghao') && context?.$toolUtil.storageGet("frontRole")!="管理员"){
				form.value.kehuzhanghao = json.kehuzhanghao
				disabledForm.value.kehuzhanghao = true;
			}
			if(json.hasOwnProperty('kehuxingming') && context?.$toolUtil.storageGet("frontRole")!="管理员"){
				form.value.kehuxingming = json.kehuxingming
				disabledForm.value.kehuxingming = true;
			}
			if(json.hasOwnProperty('shoujihaoma') && context?.$toolUtil.storageGet("frontRole")!="管理员"){
				form.value.shoujihaoma = json.shoujihaoma
				disabledForm.value.shoujihaoma = true;
			}
		})
	}
	//初始化
	//取消
	const backClick = () => {
		history.back()
	}
	//提交
	const save=()=>{
		var table = crossTable.value
		var objcross = JSON.parse(JSON.stringify(crossRow.value))
		if(objcross!='') {
			objcross.shuliang = objcross.shuliang - form.value.shuliang
			if(objcross.shuliang<0){
				context?.$toolUtil.message('数量不足','error')
				return false
			}
		}
		let crossUserId = ''
		let crossRefId = ''
		let crossOptNum = ''
		if(type.value == 'cross'){
			if(crossColumnName.value!=''){
				if(!crossColumnName.value.startsWith('[')){
					for(let o in objcross){
						if(o == crossColumnName.value){
							objcross[o] = crossColumnValue.value
						}
					}
					//修改跨表数据
					changeCrossData(objcross)
				}else{
					crossUserId = context?.$toolUtil.storageGet('userid')
					crossRefId = objcross['id']
					crossOptNum = crossColumnName.value.replace(/\[/,"").replace(/\]/,"")
				}
			}
		}
		formRef.value.validate((valid)=>{
			if(valid){
				if(crossUserId&&crossRefId){
					form.value.crossuserid = crossUserId
					form.value.crossrefid = crossRefId
					let params = {
						page: 1,
						limit: 1000, 
						crossuserid:form.value.crossuserid,
						crossrefid:form.value.crossrefid,
					}
					context?.$http({
						url: `${tableName}/page`,
						method: 'get', 
						params: params 
					}).then(res=>{
						if(res.data.data.total>=crossOptNum){
							context?.$toolUtil.message(`${crossTips.value}`,'error')
							return false
						}else{
							context?.$http({
								url: `${tableName}/${!form.value.id ? "save" : "update"}`,
								method: 'post', 
								data: form.value 
							}).then(res=>{
								context?.$toolUtil.message(`操作成功`,'success',()=>{
									//修改跨表数据
									changeCrossData(objcross)
									history.back()
								})
							})
						}
					})
				}else{
					context?.$http({
						url: `${tableName}/${!form.value.id ? "save" : "update"}`,
						method: 'post', 
						data: form.value 
					}).then(res=>{
						context?.$toolUtil.message(`操作成功`,'success',()=>{
							//修改跨表数据
							changeCrossData(objcross)
							history.back()
						})
					})
				}
			}
		})
	}
	//修改跨表数据
	const changeCrossData=(row)=>{
		context?.$http({
			url: `${crossTable.value}/update`,
			method: 'post',
			data: row
		}).then(res=>{})
	}
	onMounted(()=>{
		type.value = route.query.type?route.query.type:'add'
		let row = null
		let table = null
		let statusColumnName = null
		let tips = null
		let statusColumnValue = null
		if(type.value == 'cross'){
			row = context?.$toolUtil.storageGet('crossObj')?JSON.parse(context?.$toolUtil.storageGet('crossObj')):{}
			table = context?.$toolUtil.storageGet('crossTable')
			statusColumnName = context?.$toolUtil.storageGet('crossStatusColumnName')
			tips = context?.$toolUtil.storageGet('crossTips')
			statusColumnValue = context?.$toolUtil.storageGet('crossStatusColumnValue')
		}
		init(route.query.id?route.query.id:null, type.value,'', row, table, statusColumnName, tips, statusColumnValue)
	})
	
</script>
<style lang="scss" scoped>
	// 面包屑盒子
	.bread_view {
		border-radius: 0px;
		padding: 12px 20px;
		margin: 0px auto;
		background: none;
		width: 100%;
		border-color: #eee;
		border-width: 0 0 0px;
		position: relative;
		border-style: solid;
		:deep(.breadcrumb) {
			font-size: 14px;
			line-height: 1;
			.el-breadcrumb__separator {
				margin: 0 9px;
				color: #999;
				font-weight: 500;
			}
			.first_breadcrumb {
				.el-breadcrumb__inner {
					color: #333;
					display: inline-block;
				}
			}
			.second_breadcrumb {
				.el-breadcrumb__inner {
					color: #999;
					display: inline-block;
				}
			}
		}
	}
	// 表单
	.add_form{
		border: 0px solid #2da065;
		border-radius: 0px;
		padding: 30px 7%;
		background: none;
		// form item
		:deep(.el-form-item) {
			border: 0px solid #eee;
			padding: 6px 0;
			margin: 0 0 20px 0;
			background: none;
			display: flex;
			//label
			.el-form-item__label {
			 background: none;
			 display: block;
			 width: auto;
			 min-width: 150px;
			 text-align: right;
			}
			// 内容盒子
			.el-form-item__content {
				display: flex;
				width: calc(100% - 150px);
				justify-content: flex-start;
				align-items: center;
				flex-wrap: wrap;
				// 输入框
				.list_inp {
					padding: 0 10px;
					background: none;
					width: auto;
					border-color: #ccc;
					border-width: 1px;
					line-height: 36px;
					box-sizing: border-box;
					border-style: solid;
					min-width: 100%;
					height: 36px;
					//去掉默认样式
					.el-input__wrapper{
						border: none;
						box-shadow: none;
						background: none;
						border-radius: 0;
						height: 100%;
						padding: 0;
					}
					.is-focus {
						box-shadow: none !important;
					}
				}
				//日期选择器
				.list_date {
					border-radius: 0px;
					background: none;
					width: auto;
					border-color: #ccc;
					border-width: 1px;
					line-height: 36px;
					box-sizing: border-box;
					border-style: solid;
					min-width: 100%;
					//去掉默认样式
					.el-input__wrapper{
						border: none;
						box-shadow: none;
						background: none;
						border-radius: 0;
						height: 100%;
					}
				}
			}
		}
	}
	// 按钮盒子
	.formModel_btn_box {
		padding: 0 150px;
		display: flex;
		width: 100%;
		justify-content: flex-start;
		align-items: center;
		.formModel_cancel {
			border: 0px solid #27bacc;
			cursor: pointer;
			border-radius: 0px;
			padding: 0 40px;
			margin: 0 20px 0 0;
			color: #fff;
			background: #999;
			width: auto;
			font-size: 14px;
			line-height: 40px;
			transition: all 0.3s;
			height: 40px;
		}
		.formModel_cancel:hover {
		}
		
		.formModel_confirm {
			border: 0;
			cursor: pointer;
			border-radius: 0px;
			padding: 0 40px;
			margin: 0 20px 0 0;
			color: #fff;
			background: #113961;
			width: auto;
			font-size: 14px;
			line-height: 40px;
			transition: all 0.3s;
			height: 40px;
		}
		.formModel_confirm:hover {
		}
	}
</style>